.mask{
    width:100%;
    height:100vh;
    position:relative;
}
.into-img{
    width:100%;
    height:100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content:"";
    width:100%;
    position:absolute;
    top:0;
    left: 0;
    background-color: black;
    opacity: 0.8;
}
.content p{
    color:rgb(0, 255, 115);
    font-weight: 2rem;
    font-size: 1.5rem;
}
.hero{
    height:100%;
    width:100%;
}
.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}
.content span{
    color: red;
}
.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 0.2rem;
}

.hero-img{
    width:100%;
    background: rgba(0,0,0,0.5);
    height:60vh;
    position:relative;
}
.hero-img::before{
    content: "";
    background: url("https://images.unsplash.com/photo-1590959651373-a3db0f38a961?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=739&q=80");
        background-size: cover;
        background-position: center top;
    height:100%;
    width:100%;
    position:absolute;
    top:0;
    left:0;
    z-index:-1;
}
.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1 {
    font-size: 2.4rem;
}

.hero-img p {
    font-size: 1.4rem;
    text-align: center;
}


@media screen and (max-width:640px){
    .hero .content h1{
        font-size: 2rem;
    }
        .hero .content p {
            font-size: 1.2rem;
        }
        .hero-img h1 {
            font-size: 2rem;
        }
}